/*---------Basic Setup---------*/

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  color: #555;
  font-family: 'Lato', 'Arial', sans-serif;
  font-weight: 300;
  font-size: 20px;
  text-rendering: optimizeLegibility;
}

/*---------Reusable Components---------*/

.row {
  max-width: 1140px;
  margin: 0 auto;
}
body {
  margin: 0;
  padding: 0;
}


h1 {
  color: #fff;
  font-size: 240%;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 1px;
  word-spacing: 4px;
  margin-top: 0;
  margin-bottom: 20px;
}

.btn:link,
.btn:visited {
  display: inline-block;
  padding: 10px 30px;
  font-weight: 300;
  border: #555 solid 1px;
  border-radius: 200px;
  text-decoration: none;
  -webkit-transition: background-color 0.2s, border 0.2s, color 0.2s;
  -o-transition: background-color 0.2s, border 0.2s, color 0.2s;
  transition: background-color 0.2s, border 0.2s, color 0.2s;
}

.btn-full:link,
.btn-full:visited {
  background-color: #e67e22;
  color: #fff;
}

.btn-ghost:link,
.btn-ghost:visited {
  border: 1px solid #e67e22;
  color: #e67e22;
  margin-left: .4%;

}

.btn:hover,
.btn:active {
  background-color: #cf6d17;
}

.btn-full:hover,
.btn-full:active {
  background-color: #cf6d17;
  color: #fff;
}

.btn-ghost:hover,
.btn-ghost:active {
  border: 1px solid #cf6d17;
  color: #fff;
}

/*---------Header---------*/

header {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0,0,0, 0.7))), url(/images/Eataly-Hero.jpg);
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0,0,0, 0.7)), url(/images/Eataly-Hero.jpg);
  background-image: -o-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0,0,0, 0.7)), url(/images/Eataly-Hero.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0,0,0, 0.7)), url(/images/Eataly-Hero.jpg);
  background-position: center;
  background-size: cover;
  height: 100vh;
}

.hero-text-box {
  position: absolute;
  width: 1140px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.logo {
  margin-top: 20px;
  height: 100px;
  width: auto;
  float: left;
}

.main-nav {
  margin-top: 30px;
  float: right;
  list-style: none;
}

.main-nav li {
  display: inline-block;
  margin: 30px;
}

.main-nav li a:link,
.main-nav li a:visited {
  padding: 3px 0;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 80%;
  border-bottom: 2px solid transparent;
  -webkit-transition: border-bottom 0.2s;
  -o-transition: border-bottom 0.2s;
  transition: border-bottom 0.2s;
}

.main-nav li a:hover,
.main-nav li a:active {
  border-bottom: 2px solid #e67e22;
}



